





























import { Component, Vue, Prop } from 'vue-property-decorator';

export type item = {
  id: number;
  type: number;
  value: number;
  img: string;
};

@Component
export default class ItemSelection extends Vue {
  selected = null;

  @Prop({ required: true })
  items!: item[];

  static isPath(path: string): boolean {
    return path.includes('/');
  }
}

